import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { Observable } from "rxjs";
import { Router } from '@angular/router';
import { DataSource } from '@angular/cdk/collections';
import { MatDialog } from '@angular/material';
import { HttpClient } from '@angular/common/http';
import { MatTableDataSource, MatPaginator } from '@angular/material';
import { Gst } from './gst';
import { GstService } from './gst.service';
import {AddgstComponent} from './addgst/addgst.component';
import{DeletegstComponent} from './deletegst/deletegst.component';
import {EditgstComponent} from './editgst/editgst.component';
@Component({
  selector: 'app-gst',
  templateUrl: './gst.component.html',
  styleUrls: ['./gst.component.css']
})
export class GstComponent implements OnInit {

  private customer: Gst[];
  exampleDatabase: GstService | null;
  dataSource;
  displayedColumns = ['gstId', 'gstType', 'gst', 'igst','cgst','sgst', 'actions'];
  constructor(private _gstservice: GstService,
    private _router: Router,
    private _http: HttpClient,
    private _dialog: MatDialog) { }
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild('filter') filter: ElementRef;

  ngOnInit() {
    this.gstlist();
  }

  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // MatTableDataSource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }
  gstlist() {
    this._gstservice.getGst().subscribe((gst) => {
      console.log(gst)
      this.dataSource = new MatTableDataSource(gst);
      this.dataSource.paginator = this.paginator;
    }, (error) => {
      console.log(error);
    });
  }
  addGst(gst: Gst) {
    const dialogRef = this._dialog.open(AddgstComponent, {
      data: { gst: gst }  
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result === 1) {
        this.gstlist();
      }
    });
  }
  editGst(i:number,gstId:number,gstType:string,gst:number,igst:number,cgct:number,sgst:number){
    const dialogRef = this._dialog.open(EditgstComponent, {
      data: { gstId: gstId,gstType:gstType,gst:gst,igst:igst,cgct:cgct, sgst:sgst}  
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result === 1) {
        this.gstlist();
      }
    }); 
  }
  deleteGst(i:number,gstId:number,gstType:string,gst:number){
    const dialogRef = this._dialog.open(DeletegstComponent, {
      data: { gstId: gstId,gstType:gstType,gst:gst}  
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result === 1) {
        this.gstlist();
      }
    }); 
  }
  gstslist(){
    this.gstlist();
  }
}
