import { Component, OnInit, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import {GstService} from '../gst.service';
import {Gst} from '../gst';
@Component({
  selector: 'app-addgst',
  templateUrl: './addgst.component.html',
  styleUrls: ['./addgst.component.css']
})
export class AddgstComponent implements OnInit {

  private  igst:number;
  private cgst:number;
  private sgst:number;
  private gst:Gst;
  constructor(public dialogRef: MatDialogRef<AddgstComponent>,
    @Inject(MAT_DIALOG_DATA) public data: Gst,
    private gstService: GstService) { }

  ngOnInit() {
    this.gst=this.gstService.getter();
    this.gst=new Gst();
  }
  gsts(gst:number,gstType:string){
    console.log(gst,'',gstType);
    if(gstType=='intergstdealer'){
      this.igst=0;
      this.sgst=gst/2;
      this.cgst=gst/2;
    }else if(gstType=='intragstdealer'){
      this.igst=gst;
      this.sgst=0;
      this.cgst=0;
    }
    console.log('IGST',this.igst,'SGST',this.sgst,'CGST',this.cgst);
  }
  saveGst(){
    this.gst.igst=this.igst;
    this.gst.sgst=this.sgst;
    this.gst.cgst=this.cgst;
    console.log('GST',this.gst);
    this.gstService.saveGst(this.gst).subscribe((data)=>{
      console.log(data);
      alert("Save Successfully");
    },(error)=>{
      console.log(error);
    });
  }
  onNoClick(): void {
    this.dialogRef.close();
  }
}
