import { Component, OnInit, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import { GstService } from '../gst.service';
import { Gst } from '../gst';
@Component({
  selector: 'app-deletegst',
  templateUrl: './deletegst.component.html',
  styleUrls: ['./deletegst.component.css']
})
export class DeletegstComponent implements OnInit {

  constructor(public dialogRef: MatDialogRef<DeletegstComponent>,
    @Inject(MAT_DIALOG_DATA) public data: Gst,
    private gstService: GstService) { }

  ngOnInit() {
    console.log(this.data);
  }
  onNoClick(): void {
    this.dialogRef.close();
  }

  deleteGst(){
    this.gstService.deleteGst(this.data.gstId).subscribe((data)=>{
      alert("Delete Successfully");
    },(error)=>{
      console.log(error);
    })
  }
}
