import { Component, OnInit, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import { GstService } from '../gst.service';
import { Gst } from '../gst';

@Component({
  selector: 'app-editgst',
  templateUrl: './editgst.component.html',
  styleUrls: ['./editgst.component.css']
})
export class EditgstComponent implements OnInit {
  private igst: number;
  private cgst: number;
  private sgst: number;
  private gst: Gst;
  private gst1: number;
  constructor(public dialogRef: MatDialogRef<EditgstComponent>,
    @Inject(MAT_DIALOG_DATA) public data: Gst,
    private gstService: GstService) { }

  ngOnInit() {
    console.log('edit',this.data)
    this.gst1 = this.data.gst;
  }

  gsts(gst: number, gstType: string) {
    console.log(gst, '', gstType);
    if (gstType == 'gstdealer') {
      this.igst = 0;
      this.sgst = gst / 2;
      this.cgst = gst / 2;
    } else if (gstType == 'otherdealer') {
      this.igst = gst;
      this.sgst = 0;
      this.cgst = 0;
    }
    console.log('IGST', this.igst, 'SGST', this.sgst, 'CGST', this.cgst);
  }
  updategst() {
    if (this.gst1 == this.data.gst) {
      console.log(this.data)
      this.gstService.updateGst(this.gst).subscribe((data)=>{
        alert("Update Successfully");
      },(error)=>{
        console.log(error);
      });
    } else {
      this.data.igst = this.igst;
      this.data.sgst = this.sgst;
      this.data.cgst = this.cgst;
      console.log('GST', this.gst);
      console.log('change');
      this.gstService.updateGst(this.gst).subscribe((data)=>{
        alert("Update Successfully");
      },(error)=>{
        console.log(error);
      });
    }
    
  }
  onNoClick(): void {
    this.dialogRef.close();
  }
}
